import React, { useRef } from 'react'
import emailjs from '@emailjs/browser'
import Title from './Title'

export default function OrderForm() {
	const formRef = useRef()

	const sendEmail = e => {
		e.preventDefault()

		emailjs
			.sendForm(
				'service_cu1awmm', // ID   EmailJS
				'template_lq5sfgo', // ID 
				formRef.current,
				'Fm1mVQCL0PlOlNGEh' //  
			)
			.then(
				result => {
					console.log(' !', result.text)
					alert('  !')
					e.target.reset()
				},
				error => {
					console.error('  ', error.text)
					alert(' .   .')
				}
			)
	}

	return (
		<div className='order_form'>
			<Title
				smallTitle=' '
				bigTitle='      '
			/>
			<form ref={formRef} onSubmit={sendEmail} className='form'>
				<input
					className='contact_input order_form'
					placeholder=' '
					name='name'
					required
				/>
				<input
					className='contact_input order_form'
					placeholder=' '
					name='company_name'
				/>
				<input
					className='contact_input order_form'
					placeholder='*'
					name='user_phone'
					required
				/>
				<input
					className='contact_input order_form'
					placeholder='Email'
					name='user_email'
					type='email'
				/>
				<textarea
					placeholder='  '
					className='contact_input order_form'
					name='object_type'
				/>
				<button type='submit' className='submit_btn order_btn'>
					
				</button>
				<div className='politics'>
					<p className='p'>  ,    </p>
					<a href='#!' className='p pol_link'>
						 
					</a>
				</div>
			</form>
		</div>
	)
}
